import { useCart } from 'services/cartContext';

const Cart = () => {
  const { cart, error } = useCart();

  return (
    <>
      {error && <h1>{error.message}</h1>}
      {cart ? (
        <ul>
          {cart.map(({ _id, name }) => (
            <li key={_id}>{name}</li>
          ))}
        </ul>
      ) : (
        <p>Your cart is empty.</p>
      )}
    </>
  );
};

export default Cart;
